import React, {useState} from "react";
import Map from "../components/Map/Map.jsx";
import {Button, Slider} from "@mui/material";
import SelectButton from "../components/SelectButton.jsx";
import {BFS, lovePixelArt} from "../Algorithm.jsx";
import LoveMap from "../components/LoveMap.json";
import ButtonGroup from "@mui/material/ButtonGroup";

function Yzl() {
  function preventHorizontalKeyboardNavigation(event) {
    if (event.key === 'ArrowLeft' || event.key === 'ArrowRight') {
      event.preventDefault();
    }
  }

  const solveMap = async () => {
    const [visited, path, found] = BFS(map,[17,16],[2,1], 0, 1, 2);
    //  if found, mark visited as 1, path as 2, one by one, each 0.1s
    for (let i = 0; i < visited.length; i++) {
      let tmpMap = [...map];
      tmpMap[visited[i][0]][visited[i][1]] = 2;
      setMap(tmpMap);
      await new Promise(r => setTimeout(r, 10));
    }
    if (found) {
      for (let i = 0; i < path.length; i++) {
        let tmpMap = [...map];
        tmpMap[path[i][0]][path[i][1]] = 2;
        setMap(tmpMap);
        await new Promise(r => setTimeout(r, 10));
      }
    }
  }

  const [map_size, setMap_size] = React.useState(20);
  const [map, setMap] = React.useState(LoveMap.map);
  const [b_size, setB_size] = React.useState(30);

  const clearMap = () => {
    const tmpMap = LoveMap.map;
    for (let i = 0; i < map_size; i++) {
      for (let j = 0; j < map_size; j++) {
        if (tmpMap[i][j] !== 1) {
          tmpMap[i][j] = 0;
        }
      }
    }
    setMap(tmpMap);
  }

  const changeMap = (x, y) => {
    let tmpMap = [...map];
    tmpMap[x][y] = map[x][y] === 0 ? 3 : 0;
    setMap(tmpMap);
  }
  return (
    <div>
      <Slider
        sx={{
          '& input[type="range"]': {
            WebkitAppearance: 'slider-vertical',
          },
        }}
        style={{
          position: "absolute",
          top: 100,
          right: 50,
          height: "200px",
        }}
        orientation="vertical"
        defaultValue={30}
        aria-label="Temperature"
        valueLabelDisplay="auto"
        onKeyDown={preventHorizontalKeyboardNavigation}
        onChange={(event, value) => {
          setB_size(value);
        }}
        value={b_size}
      />
      <div style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100vh",
        width: "100vw",
        overflow: "scroll",
        overflowY: "scroll",
      }}>
        <Map size={30}
             style={{
               zoom: b_size / 30,
             }}
             map={map}
             onClickMapBlock={(x, y) => {
               changeMap(x, y);
             }}
        />
      </div>
      <div style={{
        position: "absolute",
        bottom: 40,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        width: "100%",
      }}>
        <ButtonGroup variant="contained" aria-label="outlined primary button group">
          <Button variant="contained" onClick={clearMap}>Clear</Button>
          <Button variant="contained" onClick={solveMap}>Click Me!</Button>
        </ButtonGroup>
      </div>
    </div>
  );
}

export default Yzl;