import React from "react";
import PropTypes from "prop-types";


function MapUnit(props) {
  const [hover, setHover] = React.useState(false);
  const size = props.size + "px";
  let color = "white";
  if (props.isClick === 1) {
    color = "white";
  }
  if (props.isClick === 3) {
    color = "red";
  }
  if (props.isClick === 2) {
    color = "red";
  }
  return (
    <td style={{
      border: hover ? "1px solid black" : "1px solid gray",
      backgroundColor: color,
      transition: "all 0.1s",
      // minWidth: "30px",
      // maxWidth: "30px",
      // minHeight: "30px",
      // maxHeight: "30px",
      // height: "30px",
      // width: "30px",
      width: size,
      height: size,
      minWidth: size,
      maxWidth: size,
      minHeight: size,
      maxHeight: size,
    }}
        onMouseEnter={() => {
          setHover(true);
        }}
        onMouseLeave={() => {
          setHover(false);
        }}
        onClick={props.onClick}
    />
  );
}

MapUnit.propTypes = {
  isClick: PropTypes.number.isRequired,
  size: PropTypes.number.isRequired,
}

export default MapUnit;