import React from "react";
import MapUnit from "./MapUnit.jsx";
import PropTypes from "prop-types";

function Map(props) {
  // props.style
  let style = {
    borderCollapse: "collapse",
  }
  if (props.style) {
    style = {...style, ...props.style};
  }

  return (
    <table style={style}>
      <tbody>
      {
        props.map.map((row, Rindex) => {
          return (
            <tr key={Rindex}>
              {
                row.map((col, Cindex) => {
                  return (
                    <MapUnit isClick={col} key={Cindex} size={props.size} onClick={()=>{
                      props.onClickMapBlock(Rindex, Cindex);
                    }}/>
                  );
                })
              }
            </tr>
          );
        })
      }
      </tbody>
    </table>
  )
}

Map.propTypes = {
  size: PropTypes.number.isRequired,
  style: PropTypes.object,
  map: PropTypes.array.isRequired,
  onClickMapBlock: PropTypes.func.isRequired,
}


export default Map;